import { Component, EventEmitter, Output } from "@angular/core";
import { ChipOptionModel, ChipType } from "@tei/material";
import { GetClubsListRequest } from "../../@intermediate/clubs/model/get-clubs.model";

@Component({
  selector: "tei-tee-sheet-administration-clubs-table-filter-header",
  templateUrl: "./clubs-table-filter-header.component.html",
  styleUrls: ["./clubs-table-filter-header.component.scss"]
})
export class ClubsTableFilterHeaderComponent
{
  ChipType = ChipType;
  name = '';
  id = '';

  @Output() enterFilterValueEvent = new EventEmitter<GetClubsListRequest>();

  onNameChangeEvent(chipOptionModel: ChipOptionModel): void
  {
    this.name = chipOptionModel.firstValue;
    this.submitFilterValue();
  }

  onIDChangeEvent(chipOptionModel: ChipOptionModel): void
  {
    this.id = chipOptionModel.firstValue;
    this.submitFilterValue();
  }

  submitFilterValue(): void
  {
    this.enterFilterValueEvent.emit(
      {
        ...(this.name && { ClubName: this.name }),
        ...(this.id && { ClubCode: this.id }),
      }
    );
  }
} 