import { CdkDragDrop, moveItemInArray } from "@angular/cdk/drag-drop";
import { Component, Inject, OnDestroy, OnInit, ViewChild } from "@angular/core";
import { MatLegacyDialog as MatDialog } from "@angular/material/legacy-dialog";
import { MatTable } from "@angular/material/table";
import { ActivatedRoute, Router } from "@angular/router";
import { TranslateService } from "@ngx-translate/core";
import { BreadCrumb, CardDialogComponent, ConfirmationDialogComponent } from "@tei/material";
import { finalize, Subscription } from "rxjs";
import { IClubsService } from "../../../@intermediate/clubs/clubs.interface";
import { Club, GetClubsListRequest, PostClubOrderRequest } from "../../../@intermediate/clubs/model/get-clubs.model";

@Component({
  selector: "tei-tee-sheet-administration-clubs-configuration-clubs-table",
  templateUrl: "./clubs-table.component.html",
  styleUrls: ["./clubs-table.component.scss"]
})
export class ClubsConfigurationTableComponent implements OnInit, OnDestroy
{
  @ViewChild("clubsTable", { static: true }) clubsTableComponent: MatTable<Club[]>;
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.TEE_SHEET'),
      relativeUrl: '../../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.SETUP_AND_CONFIGURATION'),
      relativeUrl: '../../'
    },
    {
      title: this.translate.instant('TSADMINISTRATION.NAVIGATION.COURSE_SETUP'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant("TSADMINISTRATION.NAVIGATION.CLUBS"),
      relativeUrl: ""
    }
  ];
  clubs: Club[];
  tableColumns: string[] = ["Club", "Actions"];
  isLoading = false;
  dragDisabled = true;
  subscription = new Subscription();

  constructor(
    @Inject("IClubsService") private clubsService: IClubsService,
    private dialog: MatDialog,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private translate: TranslateService
  )
  {
  }

  ngOnInit(): void
  {
    this.load();
  }

  load(request?: GetClubsListRequest)
  {
    if (!request)
    {
      request = new GetClubsListRequest();
    }
    this.isLoading = true;
    this.subscription.add(this.clubsService.GetList(request)
      .pipe(finalize(() => { this.isLoading = false; }))
      .subscribe((result) =>
      {
        this.clubs = result.Clubs;
      }));
  }

  onFiltersChange(filteredRequest: GetClubsListRequest)
  {
    this.load(filteredRequest);
  }

  goToEditClub(club: Club): void
  {
    this.router.navigate(["edit", club.ClubUID], { relativeTo: this.activatedRoute });
  }

  deleteClub(club: Club): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: "o-confirm-dialog",
      data: {
        component: ConfirmationDialogComponent,
        dialogData: {
          title: "TSADMINISTRATION.CLUBS.DELETE_CLUB",
          content: "TSADMINISTRATION.CLUBS.DELETE_CLUB_CONFIRMATION",
          submitButtonLabel: "TSADMINISTRATION.CLUBS.DELETE",
          cancelButtonLabel: "TSADMINISTRATION.CLUBS.CANCEL"
        }
      },
      width: "400px"
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.isLoading = true;
        this.subscription.add(this.clubsService.Delete({ clubUID: club.ClubUID }).pipe(finalize(() => { this.isLoading = false; })).subscribe({
          next: () =>
          {
            this.load(); // Reload the list after deletion
          }
        }));
      }
    });
  }

  drop(event: CdkDragDrop<Club[]>)
  {
    moveItemInArray(this.clubs, event.previousIndex, event.currentIndex);
    this.clubsTableComponent.renderRows();
    const request = new PostClubOrderRequest();
    request.ClubUID = (event.item.data as Club).ClubUID;
    request.NewSortOrder = event.currentIndex + 1;
    this.isLoading = true;
    this.subscription.add(this.clubsService.SortItem(request).subscribe({
      error: (e) =>
      {
        moveItemInArray(this.clubs, event.currentIndex, event.previousIndex);
        this.clubsTableComponent.renderRows();
      },
      complete: () => { this.isLoading = false; }
    }));
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }
} 